import clsx from 'clsx'
import BasicStyles from './Basic.module.css'
import { useStore } from '../../../../store'
import { actions } from '../../../../store'

function Header() {
    const [state, dispatch] = useStore()
    const { name, jobPosition } = state

    const storeString = (element, target) => {
        dispatch(actions.setString(element, target))
    }

    return (
        <section className={BasicStyles.cvHeader}>
            <h1 
                className={clsx(BasicStyles.cvHeading, "editMode")}
                onInput={(e) => storeString(e.target)}
                contentEditable="true"
                suppressContentEditableWarning={true}
            >{name}</h1>
            <p 
                className={clsx(BasicStyles.cvJobDesc, "editMode")}
                onInput={(e) => storeString(e.target)}
                contentEditable="true"
                suppressContentEditableWarning={true}
            >{jobPosition}</p>
        </section>
    )
}

export default Header