import './App.css';
import { Fragment } from 'react'
import { Homepage, Editor, Modal } from './components';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

function App() {
  return (
    <Fragment>
        <BrowserRouter>
            <Routes>
                <Route path="/" element={<Homepage />} />
                <Route path="/editor/*" element={<Editor />} />
            </Routes>
        </BrowserRouter>
        <Modal />
    </Fragment>
  );
}

export default App;
