import { useEffect, useRef } from 'react';
import pageStyles from './Page.module.css';
import { useStore } from '../../../../store'

function Page({ children }) {
    const [state, ] = useStore()
    const { pageFont } = state
    const pageToPrint = useRef()

    const handleModal = (entries) => {
        if(entries[0].contentRect.height > 1122.52) {
            document.getElementById("modal").style.display = "block";
        }
    }

    useEffect(() => {
        let resizer = new ResizeObserver(handleModal) 
        resizer.observe(pageToPrint.current)
    }, [])
    
    return (
        <div className={pageStyles.page} size="A4">
            <div 
                id="page-to-print" 
                ref={pageToPrint}
                className={pageStyles.pageWrapper}
                style={{
                    fontFamily: pageFont
                }}
            >
                {children}
            </div>
        </div>  
    )
}

export default Page