import {
    SET_UNIT_SUBTITLE,
    SET_UNIT_INFODESC,
    SET_UNIT_DATE,
    DELETE_LINE,
    ADD_LINE,
    ADD_LINE_GROUP,
    SET_STRING,
    SET_FONT,
    SET_PROFILE_PHOTO,
    SET_LIST_DESC,
} from './constants.js'

export const setUnitSubtitle = (...payload) => {
    return {
        type: SET_UNIT_SUBTITLE,
        payload
    }
}

export const setUnitDate = (...payload) => {
    return {
        type: SET_UNIT_DATE,
        payload
    }
}

export const setUnitInfoDesc = (...payload) => {
    return {
        type: SET_UNIT_INFODESC,
        payload
    }
}

export const deleteLine = (...payload) => {
    return {
        type: DELETE_LINE,
        payload
    }
}

export const addLine = (...payload) => {
    return {
        type: ADD_LINE,
        payload
    }
}

export const addLineGroup = (...payload) => {
    return {
        type: ADD_LINE_GROUP,
        payload
    }
}

export const setString = (...payload) => {
    return {
        type: SET_STRING,
        payload
    }
}

export const setFont = payload => {
    return {
        type: SET_FONT,
        payload
    }
}

export const setProfilePhoto = payload => {
    return {
        type: SET_PROFILE_PHOTO,
        payload
    }
}

export const setListDesc = (...payload) => {
    return {
        type: SET_LIST_DESC,
        payload
    }
}
