import { useRef, useEffect } from 'react'
import clsx from 'clsx'
import ModalStyles from './Modal.module.css'

function Modal() {
    let modalCloseRef = useRef()
    let modal = useRef()

    const handleCloseModal = () => {
        modal.current.style.display = "none"
    }

    useEffect(() => {
        let modalClose = modalCloseRef.current
        modalClose.addEventListener("click", handleCloseModal)
        return () => {
            modalClose.removeEventListener("click", handleCloseModal)
        }
    }, [])

    return (
        <section ref={modal} className={ModalStyles.modalContainer} id="modal">
            <div className={ModalStyles.modalOverlay}></div>
            <div className={ModalStyles.modalBody}>
                <div className={ModalStyles.modalBox}>
                    <div className={ModalStyles.modalClose}>
                        <button 
                            className={clsx("btn", ModalStyles.modalCloseBtn)}
                            ref={modalCloseRef}
                        >
                            <i className="fas fa-times"></i>
                        </button>
                    </div>
                    <div 
                        className={ModalStyles.modalPhoto}
                        style={{
                            backgroundImage: "url(https://i.pinimg.com/736x/12/89/a7/1289a77e5cc98954485931150ddf0ddd--under-construction-illustrations.jpg)"
                        }}
                    ></div>
                    <div className={ModalStyles.modalTextWrapper}>
                        <h2 className={ModalStyles.modalText}>
                            Oops...Nội dung của bạn đã vượt quá 1 trang. Bạn vui lòng tóm gọn lại nhé!
                        </h2>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Modal