import { useEffect } from 'react';
const zoomInterval = 0.1524145;

function NavZoom() {
    useEffect(() => {
        document.querySelector(".nav__tools-zoomin").addEventListener("click", handleZoomIn)
        document.querySelector(".nav__tools-zoomout").addEventListener("click", handleZoomOut)
    }, [])

    const handleZoomIn = () => {
        let zoomElement = document.getElementById("page-content")
        let val = zoomElement.getBoundingClientRect().width / zoomElement.offsetWidth
        if (val < 1.4) {
            zoomElement.style['transform'] = `scale(${val + zoomInterval})`;
        }
    }

    const handleZoomOut = () => {
        let zoomElement = document.getElementById("page-content")
        let val = zoomElement.getBoundingClientRect().width / zoomElement.offsetWidth
        if (val > 0.6) {
            zoomElement.style['transform'] = `scale(${val - zoomInterval})`;
        }
    }

    return (
        <div className="nav__tool-zoom">
            <button className="btn nav__tool-btn nav__tools-zoomin">
                <i className="fas fa-search-plus"></i>
            </button>
            <button className="btn nav__tool-btn nav__tools-zoomout">
                <i className="fas fa-search-minus"></i>
            </button>
        </div>
    );
}

export default NavZoom;