import { useState } from 'react';
import TemplateSubMenu from './TemplateSubMenu.js';

function NavTemplate() {
    const [toggle, setToggle] = useState(false)
    return (
        <div 
            onClick={e => setToggle(!toggle)}
            className={toggle === true ? "btn nav__template active" : "btn nav__template"} 
        >
            <i className="far fa-copy"></i>
            <span className="nav__template-txt"> Mẫu cv</span>
            <TemplateSubMenu className={toggle === true ? "visible" : "hidden"}/>
        </div>
    )
}

export default NavTemplate