function reducer(state, action) {
    switch(action.type) {
        case SET_UNIT_SUBTITLE:
            if (action.payload[1]) {
                state[action.payload[2]][action.payload[0]].subtitle = action.payload[1].textContent
            }
            console.log(state['experts'])
            return state
        case SET_UNIT_INFODESC:
            if (action.payload[1]) {
                state[action.payload[2]][action.payload[0]].infoDesc = action.payload[1].textContent
            }
            return state
        case SET_UNIT_DATE:
            if (action.payload[1]) {
                state[action.payload[2]][action.payload[0]].date = action.payload[1].textContent
            }
            console.log(state['experts'])
            return state  
        case DELETE_LINE:
            if (state[action.payload[1]].length > 1) {
                let newUnit = state[action.payload[1]].filter((line, index) => index !== action.payload[0])
                state[action.payload[1]] = newUnit
            }
            return state
        case ADD_LINE:
            let newLine = { 
                subtitle: "your title", 
                infoDesc: "your description"
            }
            state[action.payload[1]].splice(action.payload[0] + 1, 0, newLine)
            return state
        case ADD_LINE_GROUP:
            let newLineGroup = { 
                date: "2013/05 – 2013/09",
                subtitle: "Your title",
                jobs: [
                    'Description 1',
                    'Description 2'
                ]
            }
            state[action.payload[1]].splice(action.payload[0] + 1, 0, newLineGroup)
            return state
        case SET_STRING:
            if (action.payload[0]) {
                state[action.payload[1]] = action.payload[0].textContent
            }
            return state
        case SET_FONT:
            let newState = {
                ...state,
                pageFont: action.payload
            }
            return Object.assign({}, state, newState)
        case SET_PROFILE_PHOTO:
            if (action.payload) {
                state.profilePhotoURL = action.payload
            }
            return state
        case SET_LIST_DESC:
            state[action.payload[2]][action.payload[0]].jobs = action.payload[1]
            return state
        default:
            throw new Error("Invalid action...")
    }
}

export { initState }
export default reducer