import { useEffect } from 'react';
window.jsPDF = require('jspdf');
window.html2canvas = require('html2canvas');

function NavDownload() {
    useEffect(() => {
        document.getElementById("download-pdf-btn").addEventListener("mousedown", handleDownLoadPDF)
    }, [])

    const handleDownLoadPDF = () => {
        document.getElementById("loader-wrapper").style.display = "block"
        document.getElementById("downloadIcon").style.display = "none"
        setTimeout(() => {
            document.getElementById("downloadIcon").style.display = "block"
            document.getElementById("loader-wrapper").style.display = "none"
        }, 2000)

        var pageToPrint = document.getElementById('page-to-print');
        const doc = new window.jsPDF.jsPDF({
            format: "a4",
            orientation: "portrait",
            unit: "mm",
        });

        window.html2canvas(pageToPrint, { scale: 2 }).then((canvas) => {
            const imgData = canvas.toDataURL('image/png');
            const imgProps= doc.getImageProperties(imgData);
            const pdfWidth = doc.internal.pageSize.getWidth();
            const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
            doc.addImage(imgData, 'PNG', 0, 0, pdfWidth, pdfHeight);
            doc.save(`angelcv.pdf`);
        })
    }

    return (
        <div className="nav__download" id="download-pdf-btn">
            <div id="loader-wrapper">
                <div className="loader-4"><span></span></div>
            </div>
            <i id="downloadIcon" className="fas fa-download"></i>
            <span className="nav__download-txt">Tải xuống</span>
        </div>
    );
}

export default NavDownload;