import { useEffect } from 'react';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "../slick-override.css";
import Slider from 'react-slick';
import { Link } from 'react-router-dom';

function SlickTemplate() {
    useEffect(() => {
        document.querySelectorAll(".slick__template-item").forEach(template => {
            template.addEventListener("mousedown", handleTemplateActive)
        })
    }, [])
    
    const settings = {
        dots: false,
        speed: 800,
        slidesToShow: 1,
        slidesToScroll: 1,
    };
    
    function handleTemplateActive() {
        let curActiveImg = document.querySelector(".slick__template-img.image-active")
        curActiveImg.className = curActiveImg.className.replace(" image-active", "")
        let curActiveName = document.querySelector(".slick__template-name.active")
        curActiveName.className = curActiveName.className.replace(" active", "")
        let thisTemplateImg = this.querySelector("a>.slick__template-img")
        thisTemplateImg.className += " image-active"
        let thisTemplateName = this.querySelector("a>.slick__template-name")
        thisTemplateName.className += " active"
    }

    return (
        <Slider {...settings}>
            <ul className="slick__template-list"> 
                <li className="slick__template-item">
                    <Link to="chicken-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/chicken.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Chicken</h4>
                    </Link>
                </li>
                <li className="slick__template-item">
                    <Link to="basic-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img image-active"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/basic.png')"}}
                        ></div>
                        <h4 className="slick__template-name active">Basic</h4>
                    </Link>
                </li>
                <li className="slick__template-item">
                    <Link to="cascade-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/cascade.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Cascade</h4>
                    </Link>
                </li>
                <li className="slick__template-item">
                    <Link to="crisp-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/crisp.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Crisp</h4>
                    </Link>
                </li>
            </ul>
            <ul className="slick__template-list"> 
                <li className="slick__template-item">
                    <Link to="iconic-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/iconic.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Iconic</h4>
                    </Link>
                </li>
                <li className="slick__template-item">
                    <Link to="vibes-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/vibes.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Vibes</h4>
                    </Link>
                </li>
                <li className="slick__template-item">
                    <Link to="simple-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/simple.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Simple</h4>
                    </Link>
                </li>
                <li className="slick__template-item">
                    <Link to="enfold-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/enfold.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Enfold</h4>
                    </Link>
                </li>
            </ul>
        </Slider>
    );
}

export default SlickTemplate