function NavDesign() {

    const handleEdit = (type) => {
        document.execCommand(type)
    }

    return (
        <div className="nav__design">
            <button 
                data-edit="bold" 
                className="btn nav__design-btn"
                onMouseDown={() => handleEdit("bold")}
            >
                <i className="fas fa-bold"></i>
            </button>
            <button 
                data-edit="italic" 
                className="btn nav__design-btn nav__design-italic"
                onMouseDown={() => handleEdit("italic")}
            >
                <i className="fas fa-italic"></i>
            </button>
            <button 
                data-edit="underline" 
                className="btn nav__design-btn nav__design-underline"
                onMouseDown={() => handleEdit("underline")}
            >
                <i className="fas fa-underline"></i>
            </button>
        </div>
    );
}

export default NavDesign;