import { useEffect } from 'react';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "../slick-override.css";
import Slider from 'react-slick';
import { useStore, actions } from '../../../../store';

function SlickFont() {
    const [state, dispatch] = useStore()
    const { pageFont } = state

    useEffect(() => {
        document.querySelectorAll(".slick__fonts-item").forEach(item => {
            item.addEventListener("click", handleActiveFont)
        })
    })
    
    const settings = {
        dots: false,
        speed: 600,
        slidesToShow: 1,
        slidesToScroll: 1,
    };
    
    useEffect(() => {
        document.querySelectorAll(".slick__fonts-item").forEach(item => {
            if (item.style.fontFamily.split(", ")[0] === pageFont.split(", ")[0]) {
                item.className += " font-active"
            }
        })
    }, [pageFont])
    
    function handleActiveFont(e) {
        e.stopPropagation()
        let currentActive = document.querySelectorAll(".font-active")[0]
        currentActive.className = currentActive.className.replace("font-active", "")
        this.className += " font-active"
        dispatch(actions.setFont(this.style.fontFamily))    
    }
    
    return (
        <Slider {...settings}>
            <ul className="slick__fonts-list">
                <li 
                    className="slick__fonts-item"
                    style={{fontFamily: 'Arial, Helvetica, sans-serif'}}
                >Arial
                </li>
                <li 
                    className="slick__fonts-item"
                    style={{fontFamily: "'Times New Roman', Times, serif"}}
                >Time News Roman
                </li>
                <li 
                    className="slick__fonts-item"
                    style={{fontFamily: "'Courgette', cursive"}}
                >Courgette
                </li>
                <li 
                    className="slick__fonts-item"
                    style={{fontFamily: "'Amiri', serif"}}
                >Amiri
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Montserrat', sans-serif"}}
                >Montserrat
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Roboto', sans-serif"}}
                >Roboto
                </li>
            </ul>
            <ul className="slick__fonts-list">
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Caveat', cursive"}}
                >Caveat
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Dosis', sans-serif"}}
                >Dois
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'EB Garamond', serif"}}
                >EB Garamond
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Hurricane', cursive"}}
                >Hurricane
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Open Sans', sans-serif"}}
                >Open Sans
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Passions Conflict', cursive"}}
                >Passions Conflict
                </li>
            </ul>
            <ul className="slick__fonts-list">
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Petemoss', cursive"}}
                >Petemoss
                </li>
                <li 
                    className="slick__fonts-item"
                    style={{fontFamily: "'Playfair Display', serif"}}
                >Playfair Display
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Poppins', sans-serif"}}
                >Poppins
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Rubik', sans-serif"}}
                >Rubik
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Dancing Script', cursive"}}
                >Dancing Script
                </li>
                <li 
                    className="slick__fonts-item" 
                    style={{fontFamily: "'Yuji Boku', serif"}}
                >Yuji Boku
                </li>
            </ul>
        </Slider>
    );
}

export default SlickFont;

