import { useState } from 'react';
import FontSubMenu from './FontSubMenu.js';

function NavFont() {
    const [toggle, setToggle] = useState(false)

    return (
        <div 
            className={toggle === true ? "btn nav__font active" : "btn nav__font"}
            onClick={() => setToggle(!toggle)}
        >
            <i className="fas fa-font"></i>
            <span className="nav__font-txt"> Phông chữ</span>
            <FontSubMenu className={toggle === true ? "visible" : "hidden"}/>
        </div>
    );
}

export default NavFont;